html {
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 10px;
    line-height: 1.4;
}

.title {
    font-size: 5rem;
    color: #ffffff;
    font-weight: 600;
}

.subtitle {
    font-size: 1.8rem;
    color: #999999;
}

.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #111111;
}

.flex {
    display: flex;
}

.none {
    display: none;
}

.logo {
    width: 20vw;
    animation: logo 2s ease-in-out 2;
}

.text-intro {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    animation: rari 2s ease-in-out  -1s 1;
}

@media (max-width: 767px) {
    .logo {
        width: 40vw;
    }
}

@keyframes logo {
    0% { opacity: 0;

    }

    50% { opacity: 1;

    }

    100% { opacity: 0;

    }
}

@keyframes rari {
    0% { opacity: 1;

    }

    50% { opacity: 0;

    }

    100% { opacity: 1;

    }
}



